import './skills.css';

import jsImage from './images/js.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPython, faJs, faJava, faUikit } from "@fortawesome/free-brands-svg-icons";
import { faDatabase } from '@fortawesome/free-solid-svg-icons'



function Skills() {

    const skillset = [
        {id: faPython, title: 'Python', showTitle: 'Python', 'description': '(Flask, Django & React)'},
        {id: faJava, title: 'Java', showTitle: 'Java', 'description': '(Android)'},
        {id: faDatabase,title: 'Database', showTitle: 'Database', 'description': '(Sqlite3, PostgresSQL & MySQL)'},
        {id: faJs,title: 'JavaScript',showTitle: 'JavaScript', 'description': '(Flask, Django & React)'},
        {id: faUikit,title: 'UI', showTitle: 'UI/UX', 'description': '(Adobe XD)'},
    ];

  return (
    <div id="skill-link" className="skills-container">
        <div className="skill-logo-box">
            <h1 id="skills-logo">
            - Skills -
            </h1>
        </div> 
        <div className="skill-section">
            <div id="js-image"> 
                <img src={jsImage} alt={jsImage} width="240px" height="100px" />
            </div>
            {skillset.map((id, key) =>
                 <div className="box" key={key}>
                     <FontAwesomeIcon  icon={id.id} size="10x" className={id.title} />
                     <h3>{id.showTitle}</h3>
                     <p> {id.description}</p>
                </div>
            )}
        </div>

    </div>
  );
}

export default Skills;