import './projects.css';
import React from 'react';
import Web from './web/web';
import SlideRoutes from 'react-slide-routes'
import {NavLink, Route, useLocation} from 'react-router-dom';
import Android from './android/android';
import Opensource from './opensource/openSource';
// import Snackbar from '@material-ui/core/Snackbar';
// import IconButton from '@material-ui/core/IconButton';
// import CloseIcon from '@material-ui/icons/Close';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAndroid} from "@fortawesome/free-brands-svg-icons";
import { faGlobe, faListAlt} from '@fortawesome/free-solid-svg-icons';
import Other from './other/other';


// class component
const Projects = () => {

    const location = useLocation();
    // apply vriables here
      return (
      <div>
        <div id="projects" className="projects-container">
            <div className="projects-logo">
             <h1>PROJECTS</h1>
            </div>
            <ul id="project-list">
                <li>
                    <NavLink exact to="/">Android <FontAwesomeIcon icon={faAndroid}/></NavLink></li>
                <li>
                    <NavLink to="/web" exact activeClassName="active">Web <FontAwesomeIcon icon={faGlobe}/></NavLink>
                </li>
                <li><NavLink exact to="/opensource">Open Source</NavLink></li>
                <li><NavLink exact to="/other">Other <FontAwesomeIcon icon={faListAlt}/></NavLink></li>
            </ul>
            <div className="slide-box">
                <SlideRoutes location={location}>
                    <Route exact path="/" component={Android}/>
                    <Route exact path="/web" component={Web} />
                    <Route exact path="/opensource" component={Opensource}/>
                    <Route exact path="/other" component={Other}/>
                    <Route exact path="/:id">
                        <p>Error 404</p>
                    </Route>
                </SlideRoutes>
            </div>
        </div>
    </div>
);
}

export default Projects;
